import SecondaryButton from 'components/SecondaryButton/SecondaryButton';
import { BtnBox, FormDescription, StyledForm } from './Form.styled';
import PrimaryButton from 'components/PrimaryButton/PrimaryButton';

const Form = ({
  onSubmit,
  isRequiredFields,
  handlePublish,
  next,
  children,
  save,
}) => {
  return (
    <StyledForm>
      <div>
        {isRequiredFields && (
          <FormDescription>
            <span>*</span> -  
          </FormDescription>
        )}
        {children}
      </div>

      <BtnBox>
        <SecondaryButton disabled type="button">
             
        </SecondaryButton>
        <SecondaryButton type="submit"> </SecondaryButton>
        {(handlePublish || next) && (
          <PrimaryButton type="button" onClick={handlePublish || next}>
            {(handlePublish && '') ||
              (next && '') ||
              (save && '')}
          </PrimaryButton>
        )}
        {save && (
          <PrimaryButton type="submit" onClick={save}>
            
          </PrimaryButton>
        )}
      </BtnBox>
    </StyledForm>
  );
};

export default Form;
